<template>
  <div class="user-nfts-section">
    <div class="section-header">
      <h2>Your Minted NFTs</h2>
    </div>
    
    <p v-if="!isAuthenticated" class="info-message">You need to login first</p>
    <p v-else-if="loading" class="info-message">Loading your NFTs...</p>
    <p v-else-if="error" class="info-message">{{ error }}</p>
    <p v-else-if="nfts.length === 0" class="info-message">You haven't minted any NFTs yet.</p>
    
    <div v-if="isAuthenticated && !loading && !error && nfts.length > 0" class="nfts-grid">
      <div v-for="nft in nfts" :key="nft.assetPublicKey" class="nft-card">
        <div class="nft-image-container">
          <img :src="nft.imageUri" :alt="nft.name" class="nft-image">
        </div>
        <div class="nft-info">
          <h3>{{ nft.name }}</h3>
          <p class="nft-status" :class="getStatusClass(nft.status)">
            {{ nft.status.charAt(0).toUpperCase() + nft.status.slice(1) }}
          </p>
          <div class="nft-actions">
            <router-link :to="`/nft/${nft.assetPublicKey}`" class="view-button">
              View Details
            </router-link>
          </div>
        </div>
      </div>
    </div>
    <AppToast v-if="toast.show" :message="toast.message" :type="toast.type" :duration="toast.duration" />
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import { useStore } from 'vuex'
import axios from 'axios'
import AppToast from '@/components/Toast.vue'

export default {
  name: 'UserNftsList',
  components: { AppToast },
  
  setup() {
    const store = useStore()
    const nfts = ref([]);
    const loading = ref(true);
    const error = ref(null);
    
    // Используем Vuex геттеры для получения состояния
    const isAuthenticated = computed(() => store.state.session.isAuthenticated)
    
    // Функция для отображения toast-уведомлений
    const showToast = (message, type = 'info') => {
      toast.value.message = message
      toast.value.type = type
      toast.value.show = true
    }
    
    // Toast state
    const toast = ref({
      show: false,
      message: '',
      type: 'info',
      duration: 7000
    })
    
    const fetchUserNfts = async () => {
      if (!isAuthenticated.value) {
        loading.value = false;
        return;
      }
      
      loading.value = true;
      error.value = null;
      
      try {
        const response = await axios.get('/nft-bitcat/user-nfts', {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
          }
        });
        
        if (response.data.success) {
          nfts.value = response.data.nfts || [];
        } else {
          error.value = response.data.message || 'Failed to load your NFTs';
          showToast(error.value, 'error')
        }
      } catch (err) {
        console.error('Error fetching user NFTs:', err);
        error.value = err.response?.data?.message || 'An error occurred while fetching your NFTs';
        showToast(error.value, 'error')
      } finally {
        loading.value = false;
      }
    };
    
    const getStatusClass = (status) => {
      switch (status) {
        case 'completed':
          return 'status-completed';
        case 'pending':
          return 'status-pending';
        case 'failed':
          return 'status-failed';
        default:
          return '';
      }
    };
    
    onMounted(() => {
      if (isAuthenticated.value) {
        fetchUserNfts();
      } else {
        loading.value = false;
      }
    });
    
    return {
      nfts,
      loading,
      error,
      isAuthenticated,
      getStatusClass,
      toast,
      showToast
    };
  }
};
</script>

