<template>
  <div class="nft-bitcat-page">
    <div class="nft-bitcat-page-wrap">
    <div class="page-header">
      <h1>BITCAT NFT Minting</h1>
      <p class="subtitle">Mint NFTs by retweeting and paying with BITCAT tokens</p>
      <button class="how-it-works-btn" @click="showHowItWorks = true">
        How it works
      </button>
    </div>
    
    <TwitterLinkSection />
    
    <!-- Tabs Navigation -->
    <div class="tabs-container">
      <div class="tabs-navigation">
        <button 
          v-for="(tab, index) in tabs" 
          :key="index" 
          @click="activeTab = index"
          :class="['tab-button', { active: activeTab === index }]"
        >
          <span class="tab-icon" v-if="index === 0"><i class="fas fa-fire"></i></span>
          <span class="tab-icon" v-else-if="index === 1"><i class="fas fa-wallet"></i></span>
          <span class="tab-icon" v-else-if="index === 2"><i class="fas fa-history"></i></span>
          {{ tab.name }}
          <span class="active-indicator" v-if="activeTab === index"></span>
        </button>
      </div>
      
      <!-- Tab Content -->
      <div class="tab-content">
        <!-- Available NFTs Tab -->
        <div v-if="activeTab === 0" class="tab-pane">
          <TwitterPostsList />
        </div>
        
        <!-- My NFTs Tab -->
        <div v-if="activeTab === 1" class="tab-pane">
          <UserNftsList />
        </div>
        
        <!-- Mint History Tab -->
        <div v-if="activeTab === 2" class="tab-pane">
          <RecentMintsList />
        </div>
      </div>
    </div>
    
    <!-- How It Works Popup -->
    <HowItWorksPopup :show="showHowItWorks" @close="showHowItWorks = false" />
  </div>
  </div>
</template>

<script>
import TwitterLinkSection from '@/components/pages/nft-bitcat/TwitterLinkSection.vue'
import TwitterPostsList from '@/components/pages/nft-bitcat/TwitterPostsList.vue'
import UserNftsList from '@/components/pages/nft-bitcat/UserNftsList.vue'
import RecentMintsList from '@/components/pages/nft-bitcat/RecentMintsList.vue'
import HowItWorksPopup from '@/components/pages/nft-bitcat/HowItWorksPopup.vue'
import { ref, onMounted, watch } from 'vue'

export default {
  name: 'NftBitcatPage',
  components: {
    TwitterLinkSection,
    TwitterPostsList,
    UserNftsList,
    RecentMintsList,
    HowItWorksPopup
  },
  setup() {
    const activeTab = ref(0);
    const showHowItWorks = ref(false);
    
    const tabs = [
      { name: 'Available', component: 'TwitterPostsList' },
      { name: 'My NFTs', component: 'UserNftsList' },
      { name: 'Mint History', component: 'RecentMintsList' }
    ];
    
    // Load active tab from localStorage on component mount
    onMounted(() => {
      const savedTab = localStorage.getItem('nft-bitcat-page-tab');
      if (savedTab !== null) {
        activeTab.value = parseInt(savedTab);
      }
    });
    
    // Save active tab to localStorage whenever it changes
    watch(activeTab, (newTabIndex) => {
      localStorage.setItem('nft-bitcat-page-tab', newTabIndex.toString());
    });
    
    return {
      activeTab,
      tabs,
      showHowItWorks
    };
  }
}
</script>

