<template>
  <div class="recent-mints-section">
    <div class="section-header">
      <h2>Recent 20 Mints</h2>
    </div>
    
    <p v-if="loading" class="info-message">Loading recent mints...</p>
    <p v-else-if="error" class="info-message">{{ error }}</p>
    <p v-else-if="nfts.length === 0" class="info-message">No recent mints found.</p>
    
    <div v-if="!loading && !error && nfts.length > 0" class="nfts-list">
      <div v-for="nft in nfts" :key="nft.assetPublicKey" class="nft-item">
        <div class="nft-image-container">
          <img :src="nft.imageUri" :alt="nft.name" class="nft-image">
        </div>
        <div class="nft-info">
          <h3>{{ nft.name }}</h3>
          <p class="nft-minted-by" v-if="nft.userTag">
            Minted by: <router-link :to="`/player-nfts/${nft.userTag}`">{{ nft.userTag }}</router-link>
          </p>
          <p class="nft-date">{{ formatDate(nft.createdAt) }}</p>
          <div class="nft-actions">
            <router-link :to="`/nft/${nft.assetPublicKey}`" class="view-button">
              View Details
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { ref, onMounted } from 'vue';

export default {
  name: 'RecentMintsList',
  
  setup() {
    const nfts = ref([]);
    const loading = ref(true);
    const error = ref(null);
    
    const fetchRecentMints = async () => {
      loading.value = true;
      error.value = null;
      
      try {
        const response = await axios.get('/nft-bitcat/recent-mints');
        
        if (response.data.success) {
          nfts.value = response.data.nfts || [];
        } else {
          error.value = response.data.message || 'Failed to load recent mints';
        }
      } catch (err) {
        console.error('Error fetching recent mints:', err);
        error.value = err.response?.data?.message || 'An error occurred while fetching recent mints';
      } finally {
        loading.value = false;
      }
    };
    
    const formatDate = (dateString) => {
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    };
    
    onMounted(() => {
      fetchRecentMints();
    });
    
    return {
      nfts,
      loading,
      error,
      formatDate
    };
  }
};
</script>

