<template>
  <div class="twitter-link-section-wrapper">
  <div class="twitter-link-section-left">
    <h2>Link your Twitter Account</h2>
    <div v-if="isLoading" class="loading">
      <p>To mint NFTs with BITCAT tokens, you need to log in first.</p>
      <router-link to="/my" class="auth-button">
        <i class="fas fa-sign-in-alt"></i> Log In
      </router-link>
    </div>
    <div v-else-if="!isAuthenticated" class="auth-required">
      <p>To mint NFTs with BITCAT tokens, you need to log in first.</p>
      <router-link to="/my" class="auth-button">
        <i class="fas fa-sign-in-alt"></i> Log In
      </router-link>
    </div>
    <div v-else-if="!hasLinkedAccount && !isLinking" class="link-form">
      <p>Just provide your username to mint NFTs</p>
      <div class="input-group">
        <span class="input-prefix">@</span>
        <input 
          v-model="twitterUsername" 
          type="text" 
          placeholder="twitter_username"
          @keyup.enter="linkAccount"
        />
      </div>
      <button class="link-button" @click="linkAccount" :disabled="isSubmitting">
        {{ isSubmitting ? 'Linking...' : 'Link Account' }}
      </button>
    </div>
    <div v-else-if="hasLinkedAccount" class="linked-account">
      <div class="linked-info">
        <p>Linked to <span class="username">@{{ linkedUsername }}</span></p>
      </div>
      <button class="unlink-button" @click="unlinkAccount" :disabled="isSubmitting">
        {{ isSubmitting ? 'Unlinking...' : 'Unlink Account' }}
      </button>
    </div>
    <!-- Toast компонент -->
    <AppToast 
      :show="toast.show" 
      :message="toast.message" 
      :type="toast.type" 
      @close="toast.show = false" 
      :duration="toast.duration"
    />
  </div>
  <div class="twitter-link-section-right">
    <h2>Go to NFT Rental Market</h2>
    <router-link to="/nft-rental" class="rental-market-button">
      <i class="fas fa-store"></i> NFT Rental
    </router-link>
  </div>
</div>

</template>

<script>
import { ref, onMounted, computed } from 'vue'
import { useStore } from 'vuex'
import AppToast from '@/components/Toast.vue'
import axios from 'axios'

export default {
  name: 'TwitterLinkSection',
  components: {
    AppToast
  },
  setup() {
    const store = useStore()
    const twitterUsername = ref('')
    const isSubmitting = ref(false)
    const isLoading = ref(true)
    const isLinking = ref(false)
    
    // Используем Vuex геттеры для получения состояния
    const isAuthenticated = computed(() => store.state.session.isAuthenticated)
    const hasLinkedAccount = computed(() => store.getters.isTwitterLinked)
    const linkedUsername = computed(() => store.getters.twitterUsername)
    
    // Toast state
    const toast = ref({
      show: false,
      message: '',
      type: 'info',
      duration: 7000
    })
    
    // Function to show toast messages
    const showToast = (message, type = 'info') => {
      toast.value.message = message
      toast.value.type = type
      toast.value.show = true
    }
    
    // Check if user has a linked Twitter account
    const checkLinkedAccount = async () => {
      isLoading.value = true
      
      // First check if user is authenticated
      if (!isAuthenticated.value) {
        isLoading.value = false
        return
      }
      
      try {
        // Используем Vuex action для проверки привязки Twitter
        await store.dispatch('checkTwitterLinked')
      } catch (error) {
        console.error('Error checking linked account:', error)
      } finally {
        isLoading.value = false
      }
    }
    
    // Link Twitter account
    const linkAccount = async () => {
      if (!twitterUsername.value.trim()) {
        return showToast('Please enter a Twitter username', 'error')
      }
      
      isSubmitting.value = true
      try {
        // Используем Vuex action для привязки Twitter
        const result = await store.dispatch('linkTwitterAccount', twitterUsername.value.trim())
        
        if (result.success) {
          showToast('Twitter account successfully linked!', 'success')
          twitterUsername.value = ''
        } else {
          showToast(result.message || 'Failed to link Twitter account', 'error')
        }
      } catch (error) {
        console.error('Error linking account:', error)
        showToast('Error linking Twitter account', 'error')
      } finally {
        isSubmitting.value = false
      }
    }
    
    // Unlink Twitter account
    const unlinkAccount = async () => {
      if (!hasLinkedAccount.value) return
      
      isSubmitting.value = true
      try {
        // Используем Vuex action для отвязки Twitter
        const result = await store.dispatch('unlinkTwitterAccount')
        
        if (result.success) {
          showToast('Twitter account successfully unlinked!', 'success')
        } else {
          showToast(result.message || 'Failed to unlink Twitter account', 'error')
        }
      } catch (error) {
        console.error('Error unlinking account:', error)
        showToast('Error unlinking Twitter account', 'error')
      } finally {
        isSubmitting.value = false
      }
    }
    
    // Initial check on component mount
    onMounted(() => {
      if (isAuthenticated.value) {
        checkLinkedAccount()
      } else {
        isLoading.value = false
      }
    })
    
    return {
      twitterUsername,
      hasLinkedAccount,
      linkedUsername,
      isAuthenticated,
      isSubmitting,
      isLoading,
      isLinking,
      linkAccount,
      unlinkAccount,
      toast
    }
  }
}
</script>

