<template>
  <div class="twitter-posts-section">
    <div class="section-header">
      <h2>NFTs for Minting</h2>
      <div class="next-check-info">
        <span>Next retweets check: </span>
        <span class="time">{{ formatNextCheckTime }}</span>
      </div>
    </div>
    
    <div v-if="isLoading" class="loading">
      <p>Loading posts...</p>
    </div>
    
    <div v-else-if="posts.length === 0" class="no-posts">
      <p>No posts available for minting at the moment.</p>
    </div>
    
    <div v-else class="posts-grid">
      <div v-for="post in posts" :key="post.postId" class="post-card">
        <div class="post-image">
          <img :src="post.imageUrl" :alt="post.title" @click="openImagePopup(post.imageUrl, post.title)" class="clickable-image" />
          <div v-if="(post.isActive && post.requiredRetweet) || (!post.requiredRetweet && post.mintedCount <= post.mintLimit - 1)" class="active-badge">{{ post.requiredRetweet ? 'Active for Retweets' : 'Active for Minting' }}</div>
        </div>
        
        <div class="post-content">
          <h3>{{ post.title }}</h3>
          <p v-if="post.description" class="post-description">{{ post.description }}</p>
          <a :href="post.twitterUrl" target="_blank" rel="noopener noreferrer" class="twitter-link">
            <i class="fab fa-twitter"></i> View on Twitter
          </a>
          <div class="post-meta">
            <div class="meta-item">
              <span class="label">{{ post.requiredRetweet ? 'Retweets are accepted until' : 'No retweet needed' }}:</span>
              <div class="time-value">{{ post.requiredRetweet ? formatDate(post.endMintingTime) : 'You can mint up to the limit' }}</div>
            </div>
            <div class="meta-stats">
              <div class="stat-item">
                <span class="label">{{ post.requiredRetweet ? 'Retweets' : 'Limit' }}:</span>
                <span>{{ post.requiredRetweet ? post.totalRetweets : post.mintLimit }}</span>
              </div>
              <div class="stat-item">
                <span class="label">Minted:</span>
                <span>{{ post.mintedCount || 0 }}</span>
              </div>
            </div>
          </div>
          
          <div class="post-actions">
            <button v-if="!post.alreadyMinted || !hasMinted[post.postId]"
              @click="checkRetweet(post.postId)" 
              :disabled="!post.requiredRetweet || (isCheckingRetweet === post.postId || hasMinted[post.postId] || post.alreadyMinted || canMint[post.postId] === true)"
              class="check-btn"
            >
              <span v-if="isCheckingRetweet === post.postId">Checking...</span>
              <span v-else-if="canMint[post.postId] === true">Retweet Verified ✓</span>
              <span v-else-if="!post.requiredRetweet">{{ post.mintedCount >= post.mintLimit ? 'Limit reached' : 'No Retweet Required' }}</span>
              <span v-else>Check My Retweet</span>
            </button>
            
            <button 
              v-if="post.alreadyMinted" 
              disabled
              class="already-minted-btn"
            >
              Already minted ✓
            </button>
            
            <button 
              v-else-if="(!post.requiredRetweet && post.mintedCount <= post.mintLimit - 1) || (post.requiredRetweet && canMint[post.postId] !== undefined && canMint[post.postId])" 
              @click="mintNft(post.postId)"
              :disabled="isMinting === post.postId"
              class="mint-btn"
            >
              {{ isMinting === post.postId ? 'Minting...' : (power.freeMint > 0 ? `Mint NFT (You have ${power.freeMint} free mints)` : `Mint NFT (50K BITCAT)`) }}
            </button>
          </div>
        </div>
      </div>
    </div>

    <AppToast 
      :show="toast.show" 
      :message="toast.message" 
      :type="toast.type" 
      @close="toast.show = false" 
      :duration="toast.duration"
    />
    
    <MintPopup 
      :show="mintPopup.show"
      :tx-signature="mintPopup.txSignature"
      :is-complete="mintPopup.isComplete"
      :nft-info="mintPopup.nftInfo"
      @close="closeMintPopup"
    />
    
    <!-- Image Popup -->
    <div v-if="imagePopup.show" class="image-popup" @click="closeImagePopup">
      <div class="image-popup-content">
        <img :src="imagePopup.url" :alt="imagePopup.alt" />
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, watch } from 'vue'
import { useWallet } from 'solana-wallets-vue'
import { useStore } from 'vuex'
import AppToast from '@/components/Toast.vue'
import MintPopup from './MintPopup.vue' // Исправляем путь импорта
import { Transaction, PublicKey, Connection } from '@solana/web3.js'
import axios from 'axios'

export default {
  name: 'TwitterPostsList',
  components: {
    AppToast,
    MintPopup
  },
  setup() {
    const wallet = useWallet()
    const store = useStore()
    
    // Image popup state
    const imagePopup = ref({
      show: false,
      url: '',
      alt: ''
    })
    const posts = ref([])
    const isLoading = ref(true)
    const lastCheckTime = ref(null)
    const isCheckingRetweet = ref(null)
    const canMint = ref({})
    const isMinting = ref(null)
    const hasMinted = ref({})
    const mintPopup = ref({
      show: false,
      txSignature: '',
      isComplete: false,
      nftInfo: {}
    })

    // Toast state
    const toast = ref({
      show: false,
      message: '',
      type: 'info',
      duration: 10000
    })
    
    // Используем Vuex геттеры для получения состояния
    const isAuthenticated = computed(() => store.state.session.isAuthenticated)
    const hasLinkedTwitter = computed(() => store.getters.isTwitterLinked)
    const power = computed(() => store.state.session.power)
    
    // Function to show toast messages
    const showToast = (message, type = 'info') => {
      toast.value.message = message
      toast.value.type = type
      toast.value.show = true
    }

    const formatNextCheckTime = computed(() => {
      if (!lastCheckTime.value) return 'Unknown'
      
      const lastCheck = new Date(lastCheckTime.value)
      const nextCheck = new Date(lastCheck.getTime() + 20 * 60 * 1000) // 20 minutes later
      return formatDate(nextCheck)
    })

    const formatDate = (dateString) => {
      const date = new Date(dateString)
      return date.toLocaleString()
    }

    const fetchPosts = async () => {
      isLoading.value = true;
      try {
        // Получаем userTag из localStorage
        const userTag = localStorage.getItem('userTag');
        
        console.log('User tag from localStorage:', userTag);
        
        // Если есть userTag, добавляем его как параметр запроса
        let url = '/nft-bitcat/twitter-posts';
        if (userTag) {
          url += `?userTag=${encodeURIComponent(userTag)}`;
        }
        
        console.log('Fetching posts with URL:', url);
        
        const response = await axios.get(url);
        const data = response.data;
        
        if (data.success) {
          posts.value = data.posts;
          
          // Find most recent check time
          const mostRecentCheck = posts.value.reduce((latest, post) => {
            if (!post.lastCheckTime) return latest;
            const postDate = new Date(post.lastCheckTime);
            return !latest || postDate > latest ? postDate : latest;
          }, null);
          
          lastCheckTime.value = mostRecentCheck;
          
          // Обрабатываем информацию о ретвитах и минтинге для каждого поста
          posts.value.forEach(post => {
            console.log(`Post ${post.postId} has retweeted:`, post.hasRetweeted);
            console.log(`Post ${post.postId} can mint:`, post.canMint);
            console.log(`Post ${post.postId} already minted:`, post.alreadyMinted);
            
            // Если пост уже сминчен, устанавливаем hasMinted
            if (post.alreadyMinted) {
              hasMinted.value[post.postId] = true;
            }
            
            // Важное исправление: Инициализируем значения для каждого поста
            if (post.hasRetweeted === true && !post.alreadyMinted) {
              canMint.value[post.postId] = true;
              console.log(`Setting canMint for post ${post.postId} to true`);
            } else {
              // Сохраняем существующее значение, если оно уже есть
              if (canMint.value[post.postId] === undefined) {
                canMint.value[post.postId] = false;
              }
            }
          });
        }
      } catch (error) {
        console.error('Error fetching posts:', error);
        showToast('Failed to load Twitter posts', 'error');
      } finally {
        isLoading.value = false;
      }
    };

    const checkRetweet = async (postId) => {
      // Проверяем авторизацию
      if (!checkAuthentication()) {
        return showToast('Please log in to check your retweet status', 'warning')
      }
      
      // Проверяем привязку Twitter
      if (!hasLinkedTwitter.value) {
        return showToast('Please link your Twitter account first', 'warning')
      }
      
      // Проверяем привязку кошелька
      if (!store.getters.hasWallet) {
        return showToast('Please connect your wallet to mint NFTs', 'warning')
      }
      
      // Если пост уже сминчен, показываем сообщение и не делаем запрос
      const post = posts.value.find(p => p.postId === postId);
      if (post && post.alreadyMinted) {
        return showToast('You have already minted an NFT for this post', 'info');
      }
      
      isCheckingRetweet.value = postId
      try {
        const response = await axios.get(`/twitter/check-retwit/${postId}`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
          }
        })
        
        const data = response.data
        if (data.success) {
          canMint.value[postId] = data.canMint
          
          if (data.canMint) {
            showToast('You can mint an NFT for this post!', 'success')
          } else {
            showToast('You need to retweet this post to mint an NFT', 'warning')
          }
        } else {
          showToast(data.message || 'Failed to check retweet', 'error')
        }
      } catch (error) {
        console.error('Error checking retweet:', error)
        showToast('Error checking retweet status', 'error')
      } finally {
        isCheckingRetweet.value = null
      }
    }

    const mintNft = async (postId) => {
      // Проверяем авторизацию
      if (!checkAuthentication()) {
        return showToast('Please log in to mint an NFT', 'warning')
      }
      
      // Проверяем привязку Twitter
      if (!hasLinkedTwitter.value) {
        return showToast('Please link your Twitter account first', 'warning')
      }
      
      // Проверяем привязку кошелька
      if (!store.getters.hasWallet) {
        return showToast('Please connect your wallet first', 'warning')
      }
      
      // Если пост уже сминчен, показываем сообщение и не делаем запрос
      const post = posts.value.find(p => p.postId === postId);
      if (post && post.alreadyMinted) {
        return showToast('You have already minted an NFT for this post', 'info');
      }
      
      isMinting.value = postId;

      try {
        // Проверяем, установлен ли Phantom Wallet
        if (!window.solana) {
          showToast('Please install Phantom Wallet extension', 'error');
          return;
        }

        // Проверяем, подключен ли кошелек
        if (!window.solana.isConnected) {
          try {
            await window.solana.connect();
          } catch (err) {
            showToast(`Error connecting to Phantom Wallet: ${err.message}`, 'error');
            isLoading.value = false;
            return;
          }
        }

        // Получаем публичный ключ пользователя
        const userWalletPublicKey = window.solana.publicKey.toString();

        // Создаем экземпляр соединения с Solana
        const connection = new Connection('https://sleek-icy-frog.solana-mainnet.quiknode.pro/9d4845e2d4c60caf45dc4f10d0bdcfdbcd6a321c', 'confirmed');

        // Получаем инструкции для минтинга от сервера
        const response = await axios.post(
          '/nft-bitcat/mint',
          {
            tweetId: postId,
            walletPublicKey: userWalletPublicKey
          },
          {
            headers: {
              'Content-Type': 'application/json',
              'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
            }
          }
        )
      
        const data = response.data
        console.log(response)
        if (data.success) {
          // Получаем экземпляр провайдера Phantom
          const provider = window.solana
          
          let signature = 'Free mint'
          if(power.value.freeMint === 0){
            
            // Десериализуем транзакцию
            const transaction = Transaction.from(Buffer.from(data.transaction, 'base64'))
              
            // Получаем последний блокхэш для транзакции
            const { blockhash } = await connection.getLatestBlockhash('confirmed');
            transaction.recentBlockhash = blockhash;
            transaction.feePayer = provider.publicKey;
              
            // Выводим информацию о BITCAT токенах
            console.log(`Mint will use ${data.bitcatAmount} BITCAT tokens`);
            
              
            // Настраиваем параметры для отображения деталей транзакции в Phantom
            const opts = {
              skipPreflight: false,
              preflightCommitment: 'confirmed',
            };
            
            // Подписываем транзакцию через Phantom Wallet API с отображением деталей
            const signedTransaction = await provider.signTransaction(transaction)
            
            // Отправляем подписанную транзакцию
            signature = await connection.sendRawTransaction(signedTransaction.serialize(), opts)
            
           
            
            // Ожидаем подтверждения транзакции (не менее 1 подтверждения)
            await connection.confirmTransaction(signature, 'confirmed')
          }

           // Показываем всплывающее окно с информацией о процессе минтинга
           mintPopup.value = {
              show: true,
              txSignature: signature,
              isComplete: false,
              nftInfo: {
                name: post?.title || 'Bitcat NFT',
                assetPublicKey: data.expectedNftAddress || '',
                imageUri: post?.imageUrl || '',
              }
            };
          
          // Обновляем статус транзакции на сервере
          const updateResponse = await axios.post('/nft-bitcat/update-tx', 
            {
              txSignature: signature,
              assetPublicKey: provider.publicKey.toString(), // Используем адрес кошелька пользователя
              postId: postId,
              metadataUri: data.metadataUri
            },
            {
              headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
              }
            }
          )
          
          if (updateResponse.data.success) {
            // Если сервер вернул информацию о NFT, используем ее
            if (updateResponse.data.nftInfo) {
              mintPopup.value.nftInfo = updateResponse.data.nftInfo;
            }
            
            // Отмечаем, что минтинг завершен
            mintPopup.value.isComplete = true;
            
            // Устанавливаем статус "Already minted" для этого поста
            post.alreadyMinted = true;
            hasMinted.value[postId] = true;
            
            showToast('NFT successfully minted!', 'success')
          } else {
            showToast('Error updating NFT status', 'error')
          }
        } else {
          showToast(data.message || 'Error preparing data for minting', 'error')
        }
      } catch (error) {
        console.error('Error minting NFT:', error)
        showToast(`Error minting NFT: ${error.message}`, 'error')
        
        // Закрываем всплывающее окно при ошибке
        if (mintPopup.value.show) {
          mintPopup.value.show = false;
        }
      } finally {
        isMinting.value = null
      }
    }

    const closeMintPopup = () => {
      mintPopup.value.show = false
    }

    const connection = new Connection('https://sleek-icy-frog.solana-mainnet.quiknode.pro/9d4845e2d4c60caf45dc4f10d0bdcfdbcd6a321c')

    // Check if user is authenticated
    const checkAuthentication = () => {
      return isAuthenticated.value
    }
    
    // Initial setup
    onMounted(async () => {
      // Check Twitter link first if user is authenticated
      if (isAuthenticated.value) {
        await store.dispatch('checkTwitterLinked')
      }
      
      // Fetch posts regardless of auth status
      fetchPosts()
    })
    
    // Watch for changes in authentication status
    watch(() => store.state.session.isAuthenticated, (newValue) => {
      if (newValue) {
        store.dispatch('checkTwitterLinked')
      }
    })
    
    // Watch for changes in Twitter link status
    watch(() => store.getters.isTwitterLinked, (newValue) => {
      console.log('Twitter link status changed:', newValue)
      // Если статус привязки Twitter изменился, обновляем состояние компонента
      if (newValue) {
        // Перезагружаем посты, чтобы обновить информацию о ретвитах
        fetchPosts()
      }
    })

    // Function to open image popup
    const openImagePopup = (url, alt) => {
      imagePopup.value = {
        show: true,
        url,
        alt
      }
    }
    
    // Function to close image popup
    const closeImagePopup = () => {
      imagePopup.value.show = false
    }
    
    return {
      posts,
      isLoading,
      isCheckingRetweet,
      isMinting,
      canMint,
      hasMinted,
      mintPopup,
      imagePopup,
      closeMintPopup,
      checkRetweet,
      mintNft,
      formatNextCheckTime,
      formatDate,
      toast,
      isAuthenticated,
      hasLinkedTwitter,
      openImagePopup,
      closeImagePopup,
      power
    }
  }
}
</script>

